    display: none !important; } }

@media (max-width: 25.875em) {
  .hidden-extra-small-down {
    display: none !important; } }

@media (max-width: 34.375em) {
  .hidden-small-phone-down {
    display: none !important; } }

@media (max-width: 47.9375em) {
  .hidden-small-down {
    display: none !important; } }

@media (max-width: 61.9375em) {
  .hidden-medium-down {
    display: none !important; } }

.move-left {
  float: left; }

.move-right {
  float: right; }

.move-inline {
  display: inline-block; }

.move-inline-block {
  display: inline-block; }

.make-block {
  display: block; }

.move-center {
  text-align: center; }

.centered {
  display: block;
  margin: 0 auto; }

.clearfix::after {
  content: '';
  display: block;
  clear: both; }

.fixed-position {
  position: fixed; }

.relative-position {
  position: relative; }

.vertically-centered {
  position: absolute;
  top: 50%;
  transform: translateY(-50%); }

.padding-1em-top {
  padding-top: 1em; }

.padding-2em-top {
  padding-top: 2em; }

.margin-1em-top {
  margin-top: 1em; }

.margin-2em-top {
  margin-top: 2em; }

.margin-1em-bottom {
  margin-bottom: 1em; }

.margin-2em-bottom {
  margin-bottom: 2em; }

.margin-1em-top-bottom {
  margin-top: 1em;
  margin-bottom: 1em; }

.margin-2em-top-bottom {
  margin-top: 2em;
  margin-bottom: 2em; }

.margin-left-1em {
  margin-left: 1em; }

.margin-left-2em {
  margin-left: 2em; }

.margin-right-1em {
  margin-right: 1em; }

.margin-right-2em {
  margin-right: 2em; }

.margin-right-3em {
  margin-right: 3em; }

.margin-bottom-none {
  margin-bottom: 0; }

@media print {
  * {
    background: transparent !important;
    color: #000 !important;
    /* Black prints faster: h5bp.com/s */
    box-shadow: none !important;
    text-shadow: none !important; }
  a,
  a:visited {
    text-decoration: underline; }
  a[href]:after {
    content: " (" attr(href) ")"; }
  abbr[title]:after {
    content: " (" attr(title) ")"; }
  .ir a:after,
  a[href^="javascript:"]:after,
  a[href^="#"]:after {
    content: ""; }
  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid; }
  thead {
    display: table-header-group;
    /* h5bp.com/t */ }
  tr,
  img {
    page-break-inside: avoid; }
  img {
    max-width: 100% !important; }
  @page {
    margin: 0.5cm; }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3; }
  h2,
  h3 {
    page-break-after: avoid; }
  .hidden-for-print {
    display: none !important; }
  .display-for-print {
    display: inline !important; } }
